<template>
	<section class="detailsPage" :state='pageState'>
		<!-- <div class="detailsPageHead">
			<img src="../images/arrowright.png" @click="history"/>
			<span>急借号</span>
		</div> -->
		<div class="wrap">
			<div class="d">
				<img :src='picUrl'/>
				<div>{{pname}}</div>
				<div>{{minDay}}-{{maxDay}}天，最高{{maxLimit}}元</div>
				<div>
					<span v-for='item in ptag'>{{item}}</span>
				</div>
				<div>
					<span>{{pBorrowNum}}</span>已申请
				</div>
			</div>
			<div class="c">
				<span>贷款金额</span>
				<span>{{minLimit}}~{{maxLimit}}元</span>
			</div>
			<div class="b">
				<div class="left">
					<span>贷款期限</span>
					<span>放款时间</span>
					<span>日利率</span>
				</div>
				<div class="right">
					<span>{{minDay}}-{{maxDay}}天</span>
					<span v-show='ploanTimeType == 1'>{{ploanMinTime}}-{{ploanMaxTime}}小时</span>
					<span v-show='ploanTimeType == 2'>{{ploanMinTime}}-{{ploanMaxTime}}天</span>
					<span v-show='ploanTimeType == 3'>{{ploanMinTime}}-{{ploanMaxTime}}分钟</span>
					<span>{{pinterest}}%</span>
				</div>
			</div>
			<div class="e">
				<div>
					<span></span>
					<span>申请条件</span>
				</div>
				<div>
					<span v-for='item in pcondition'>{{item}}</span>
				</div>
			</div>
			<div class="f">
				<div>
					<span></span>
					<span>申请流程</span>
				</div>
				<div>
					<span>{{pprocess}}</span>
				</div>
			</div>
			<div class="g">
				<div>
					<span></span>
					<span>产品说明</span>
				</div>
				<div>
					<span v-for="item in premark">{{item}}</span>
				</div>
			</div>
		</div>
		<mt-button @click="next">立即申请</mt-button>
	</section>
  
</template>
<script>
	import { getPlatformInfo, getPUrl, recode } from './../api/api'
	import { getStore } from '../config/mUtils'
export default {
  data () {
    return {
    	distailType: null, // 后退状态
    	id: null, // id值
    	pcode: null, // 编码
    	picUrl: null, // 图片地址
    	pname: null, // 产品名称
    	phttp: null, // 产品链接地址
    	minDay: null, // 贷款最小天数
    	maxDay: null, // 贷款最大天数
    	minLimit: null, // 贷款最小金额
    	maxLimit: null, // 贷款最大金额
    	pinterest: null, // 贷款利率
    	ploanMinTime: null, // 最快放款时间
    	ploanMaxTime: null, // 最慢放款时间
    	ploanTimeType: null, // 放款时间状态1小时2天
    	pcondition: null, // 申请条件
    	ptag: null, // 标签
    	premark: null, // 产品说明
    	pprocess: null, // 申请流程
    	pBorrowNum: null // 点击次数
    }
  },
  methods: {
 //    history() { // 后退
 //    	if (this.distailType === 0) {
 //    		this.$router.push({path: '/index'})
 //    	} else if (this.distailType === 1) {
 //    		this.$router.push({path: '/attestation'})
 //    	} else if (this.distailType === 3) {
 //    		this.$router.push({path: '/productList'})
 //    	}
	// },
	next() {
		let params = {
			pCode: this.pcode,
			id: this.id
		}
		getPUrl(params).then(res => {
			if (res.code === 413) {
				this.$router.push({path: '/login', query: {detailsPage: 2}})
			} else if (res.code === 200) {
				let params = {
					userId: getStore('userId'),
					positionMark: 'platRegister',
					flag: this.id
				}
				recode(params).then(res => {
					console.log(res.code)
				})
				parent.location.href = this.phttp
			}
		})
	},
	init() {
		var id = getStore('id')
		var pcode = getStore('pcode')
		let params = {
			id: id,
			pCode: pcode
		}
		getPlatformInfo(params).then(res => {
			if (res.code === 200) {
				this.pBorrowNum = res.data[0].pBorrowNum
				this.id = res.data[0].id
				this.pcode = res.data[0].pcode
				this.picUrl = res.data[0].picUrl
		    	this.pname = res.data[0].pname
		    	this.phttp = res.data[0].phttp
		    	this.minDay = res.data[0].minDay
		    	this.maxDay = res.data[0].maxDay
		    	this.minLimit = res.data[0].minLimit
		    	this.maxLimit = res.data[0].maxLimit
		    	this.pinterest = res.data[0].pinterest
		    	this.ploanMinTime = res.data[0].ploanMinTime
		    	this.ploanMaxTime = res.data[0].ploanMaxTime
		    	this.ploanTimeType = res.data[0].ploanTimeType
		    	this.pcondition = res.data[0].pcondition.split('|')
		    	this.ptag = res.data[0].ptag.split('|')
		    	this.premark = res.data[0].premark.split('|')
		    	this.pprocess = res.data[0].pprocess
		    	let params = {
					userId: getStore('userId'),
					positionMark: 'platDetail',
					flag: this.id
				}
				recode(params).then(res => {
					console.log(res.code)
				})
			}
		})
	}
  },
  computed: {
  	pageState() {
  		this.distailType = parseInt(this.$route.query.distail)
  	}
  },
  mounted() {
  	this.init()
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
body {
	background: #f2f3f2;
}
.detailsPage {
	flex:1;
	z-index: 11;
	background: #f3f5f8;
	height: 100%;
	
	// .detailsPageHead {
	// 	width: 100%;
	// 	background: white;
	// 	position: relative;
	// 	height: 0.8rem;
	// 	line-height: 0.8rem;
		
	// 	img {
	// 		transform: rotateZ(180deg);
	// 		vertical-align: middle;
	// 		width: 0.15rem;
	// 		margin-left: 0.2rem;
	// 	}
	// 	span:nth-child(2) {
	// 		vertical-align: middle;
	// 		font-size: 0.25rem;
	// 		color: black;
	// 		display: inline-block;
	// 		width: 90%;
	// 		text-align: center;
	// 	}
	// }
	.wrap {
		.d {
			display: inline-block;
			width: 100%;
			height: 2rem;
			background: white;
			margin-bottom: 0.1rem;
			position: relative;
			
			span {
				font-size: 0.2rem;
				color: #666666;
			}
			
			div:nth-child(2) {
				font-size: 0.3rem;
				position: absolute;
				top: 0.3rem;
				left: 1.9rem;
				color: #333;
				font-weight: 400;
			}
			div:nth-child(3) {
				font-size: 0.3rem;
				position: absolute;
				top: 0.8rem;
				left: 1.9rem;
				color: #333;
				
			}
			img:nth-child(1) {
				width: 1.3rem;
				height: 1.3rem;
				position: absolute;
				left: 0.3rem;
				top: 0.3rem;
				border-radius: 0.1rem;
			}
			div:nth-child(4) {
				position: absolute;
				top: 1.3rem;
				left: 1.9rem;
				
				span {
					display: inline-block;
					color: #d84949;
					border: 0.01rem solid #d84949;
					padding: 0 0.05rem;
					border-radius:0.05rem ;
					height: 0.3rem;
					line-height: 0.3rem;
				}
				span:not(:first-child) {
					margin-left: 0.1rem;
				}
			}
			div:nth-child(5) {
				position: absolute;
				top: 1.32rem;
				font-size: 0.2rem;
				right: 0.2rem;
				
				span {
					margin-right: 0.02rem;
					color:#d84949;
				}
			}
		}
		.c {
			width: 100%;
			height: 0.8rem;
			line-height: 0.8rem;
			border-top: 0.01rem solid #d6d6d6;
			background: white;
			color: #333;
			
			span {
				display: inline-block;
				font-size: 0.3rem;
				width: 46%;
			}
			span:nth-child(1) {
				margin-left: 0.2rem;
			}
			span:nth-child(2) {
				text-align: right;
			}
		}
		.b {
			width: 100%;
			background: white;
			border-top: 0.01rem solid #d6d6d6;
			
			div {
				display: inline-block;
				width: 46%;
				font-size: 0.3rem;
			}
			.left {
				margin-left: 0.2rem;
				padding: 0.2rem 0;
				span {
					display: inline-block;
					width: 100%;
					height: 0.5rem;
					line-height: 0.5rem;
				}
			}
			.right {
				text-align: right;
				padding: 0.2rem 0;
				span {
					display: inline-block;
					width: 100%;
					height: 0.5rem;
					line-height: 0.5rem;
				}
			}
		}
		.e {
			width: 100%;
			background: white;
			border-top: 0.01rem solid #d6d6d6;
			padding: 0.2rem 0;
			div:nth-child(1) {
				position: relative;
				width: 100%;
				height: 0.6rem;
				line-height: 0.6rem;
				span {
					display: inline-block;
				}
				span:nth-child(1) {
					position: absolute;
					width: 0.05rem;
					height: 0.3rem;
					background: #ef4f4f;
					top: 0.22rem;
					left: 0.2rem;
				}
				span:nth-child(2) {
					position: absolute;
					font-size: 0.3rem;
					top: 0.05rem;
					left: 0.35rem;
				}
			}
			div:nth-child(2) {
				width: 100%;
				padding-bottom: 0.2rem;
				
				span {
					display: inline-block;
					width: 80%;
					margin-left: 0.35rem;
					color: #999999;
					height: 0.4rem;
					line-height: 0.4rem;
					font-size: 0.2rem;
				}
			}
		}
		.f {
			width: 100%;
			background: white;
			border-top: 0.01rem solid #d6d6d6;
			padding: 0.2rem 0;
			div:nth-child(1) {
				position: relative;
				width: 100%;
				height: 0.6rem;
				line-height: 0.6rem;
				span {
					display: inline-block;
				}
				span:nth-child(1) {
					position: absolute;
					width: 0.05rem;
					height: 0.3rem;
					background: #ef4f4f;
					top: 0.22rem;
					left: 0.2rem;
				}
				span:nth-child(2) {
					position: absolute;
					font-size: 0.3rem;
					top: 0.05rem;
					left: 0.35rem;
				}
			}
			div:nth-child(2) {
				width: 100%;
				height: 0.5rem;
				line-height: 0.5rem;
				
				span {
					margin-left: 0.35rem;
					color: #999999;
					height: 0.4rem;
					line-height: 0.4rem;
					font-size: 0.2rem;
				}
			}
		}	
		.g {
			width: 100%;
			background: white;
			margin-bottom: 0.5rem;
			border-top: 0.01rem solid #d6d6d6;
			padding: 0.2rem 0;
			div:nth-child(1) {
				position: relative;
				width: 100%;
				height: 0.6rem;
				line-height: 0.6rem;
				
				span {
					display: inline-block;
				}
				span:nth-child(1) {
					position: absolute;
					width: 0.05rem;
					height: 0.3rem;
					background: #ef4f4f;
					top: 0.22rem;
					left: 0.2rem;
				}
				span:nth-child(2) {
					position: absolute;
					font-size: 0.3rem;
					top: 0.05rem;
					left: 0.35rem;
				}
			}
			div:nth-child(2) {
				width: 100%;
				span {
					margin-left: 0.35rem;
					color: #999999;
					margin-right: 0.35rem;
					line-height: 0.5rem;
					font-size: 0.2rem;
					display: block;
				}
			}
		}
	}
	.mint-button {
		width: 90%;
		left: 50%;
		margin-bottom: 0.5rem;
		transform: translateX(-50%);
		height: 0.8rem;
		line-height: 0.8rem;
		font-size: 0.25rem;
		border-radius: 0.5rem;
		color: #fff;
		background-image: linear-gradient(to top,#EC6B4D,#D94F4D);
		background-image: -webkit-linear-gradient(to top,#EC6B4D,#D94F4D);
	}
	
	
}
</style>



